import React, { } from 'react'
import { Button, Input, Icon, message } from 'antd'
import { Typography } from 'antd';
import { useState } from 'react';
import axios from 'axios';

const { Title } = Typography;
const PasswordUpdate = (props) => {
    const [updating, setUpdating] = useState(false)

    const [passwords, setPasswords] = useState({
        password: "",
        newPassword: "",
        confirmNewPassword: ""
    })

    const changePassword = () => {
        setUpdating(true)
        axios.post("/user/editpassword", passwords).then(() => {
            setUpdating(false)

            window.location.reload(false);
        }).catch(err => {
            setUpdating(false)

            message.error("error in update password");
        })
    }

    return (

        <div style={{ border: '1px solid lightgray', margin: '0 8px', marginBottom: '16px', height: '440px', borderRadius: '16px' }}>
            <div style={{ padding: '8px', height: '90%', margin: '0 auto', display: 'flex', flexDirection: 'column', justifyContent: 'start', alignItems: 'center' }}>
                <Title level={2}>Change password</Title>
                <br />
                <br />
                <Input.Password style={{ width: '240px' }} onChange={e => { e.persist(); setPasswords(val => ({ ...val, password: e.target.value })) }} placeholder="old password" />
                <br />
                <Input.Password style={{ width: '240px' }} onChange={e => { e.persist(); setPasswords(val => ({ ...val, newPassword: e.target.value })) }} placeholder="new password" />
                <br />
                <Input.Password style={{ width: '240px' }} onChange={e => { e.persist(); setPasswords(val => ({ ...val, confirmNewPassword: e.target.value })) }} placeholder="confrim new password" />
                <br /><br />
                <Button
                    type="primary"
                    onClick={changePassword}
                    disabled={updating}
                    loading={updating}
                    style={{ marginTop: 16 }}
                >

                    <Icon type="upload" /> {updating ? "updating" : "Update password"}
                </Button>


            </div>
        </div>

    )
}

export default PasswordUpdate
